.mainPages {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(
      rgba(46, 47, 66, 0.1),
      rgba(48, 47, 80, 0.6)
    ),
    url(../../assets/images/TB_Van_18-1536x10241.jpgw3.jpg);
}
.title {
  margin-top: 200px;
  font-size: 56px;
  color: var(--white);
}
.rentalButton {
  font-size: 20px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 80px;
  border-radius: 50%;
  background-color: var(--button);
  cursor: pointer;
  text-decoration: none;
  color: var(--main);
  transition: width 500ms cubic-bezier(0.4, 0, 0.2, 1),
    height 500ms cubic-bezier(0.4, 0, 0.2, 1),
    font-size 500ms cubic-bezier(0.4, 0, 0.2, 1),
    color 500ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.rentalButton:hover,
.rentalButton:focus {
  width: 120px;
  height: 100px;
  font-size: 26px;
  color: var(--white);
  background-color: #741d1d;
}
